*{
  margin: 0;
  padding: 0;
  list-style: none;
}
.app {
  padding: 15px;
}
.pone {
  margin-top: 20px;
  text-align: center;
  font-size: 15px;
  color: #666;
  line-height: 1.5;
  letter-spacing: 1px;
}
.desc {
  margin-top: 20px;
  font-size: 16px;
  color: #333;
  line-height: 1.4;
}
.tall__title {
  margin-top: 20px;
  font-size: 14px;
  color: #666;
}
.tall {
  padding: 10px;
}
.tall li {
  font-size: 14px;
  color: #333;
  line-height: 2;
}
.my {
  margin-top: 20px;
  text-align: center;
}
.my__title {
  margin-bottom: 10px;
  font-size: 16px;
  color: #333;
}
.my__code {
  padding: 5px;
  width: 300px;
  border-radius: 4px;
  border: 1px solid #ddd;
}
.my__text {
  font-size: 14px;
  color: #666;
  line-height: 1.5;
  text-align: left;
}
.my__desc {
  font-size: 14px;
  color: #666;
  text-align: left;
}
.code {
  margin-top: 40px;
  text-align: center;
}
.code_img {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 200px;
}
.code__title {
  font-size: 14px;
  color: #666;
}


